<template>
  <div>
    <div v-if="mode === 'gp'">
      <!--  -->
      <div v-if="path===null">
        <el-upload
          class="upload-demo"
          :action="`${API}/request/contract/upload/${this.id}`"
          :headers="auth"
          :limit="1"
          name="contract"
          :on-success="successHandle"
        >
          <el-button size="small" type="primary"></el-button>
          <template #tip>
            <div class="el-upload__tip"> 5MB </div>
          </template>
        </el-upload>
      </div>
      <!-- TODO:fileList -->
      <div v-else>
        <el-button @click="getContractFile"></el-button>
      </div>
    </div>
    <div v-else-if="mode === 'zp'">
      <!--  -->
      <el-button @click="getContractFile"></el-button>
      <br /><br /><br />
      <el-button @click="acceptContract(true)" type="primary"></el-button>
      <el-button @click="acceptContract(false)" type="warning"></el-button>
    </div>
  </div>
</template>

<script>
import { downContractFile,acceptContract } from "./api";
export default {
  data() {
    return {
      API: process.env.VUE_APP_BASE_URL,
      auth: {
        Authorization: `Bearer ${this.$store.state.token}`,
      },
      mode: null,
      id: null,
      path: null,
    };
  },
  created() {
    const q = this.$route.query;
    const mode = this.$route.params.mode;
    // --ID--ID
    this.id = q.id;
    if(q.path)this.path = q.path; // 
    this.mode = mode;
  },
  methods: {
    successHandle(response, file, fileList) {
      console.log("successHandle")
      file.name = response.data.path;
    },
    getContractFile() {
      downContractFile(this.id, this.path).then((data) => {
        this.$message({
          message: "",
          type: "success",
        });
        console.log(typeof data);
        let url = window.URL.createObjectURL(data);
        if (!data) {
          return;
        }
        let link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute(
          "download",
          this.path.split("/")[this.path.split("/").length - 1]
        );

        document.body.appendChild(link);
        link.click();
      });
    },
    acceptContract(accept) {
      // 
      acceptContract(this.id, accept).then(res=>{
        console.log(res)
        this.$message({
          message: "",
          type: "success"
        })
      })
    },
  },
};
</script>

